.footer {
  background-color: $color-black-pearl;
  min-height: 280px;

  @include vp-1023 {
    min-height: 465px;
  }
}

.footer__wrapper {
  max-width: 1294px;
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
  padding-right: 60px;
  padding-bottom: 35px;

  @include vp-1023 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 9px 30px;
    padding-top: 50px;
    padding-right: 50px;
    padding-left: 50px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 0;
    padding-top: 35px;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.footer__social-wrapper {
  display: flex;
  flex-direction: column;
  width: 28%;

  @include vp-1023 {
    grid-column: 1/-1;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    margin-bottom: 5px;
  }
}

.footer__logo {
  margin-bottom: 24px;

  @include vp-767 {
    margin-bottom: 9px;
  }
}

.footer__social-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 0;
  margin-left: 6px;
  padding: 0;


  @include vp-1023 {
    gap: 15px;
    margin-right: -6px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 8px;
  }
}

.footer__social-link {
  display: inline-block;
  vertical-align: middle;
  padding: 5px;

  @include hover {
    .footer__social-icon {
      fill: $deep-sky-blue;
    }
  }

  @include focus {
    outline: 2px solid $deep-sky-blue;
    border-radius: 5px;
  }
}

.footer__social-icon {
  width: 30px;
}

.footer__nav-wrapper {
  position: relative;

  @include vp-767 {
    &::before {
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(248, 253, 255, 0.7);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }

    &:last-child::after {
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(248, 253, 255, 0.7);
      position: absolute;
      bottom: 4px;
      left: 50%;
      transform: translateX(-50%);
    }

    padding: 12px 0;
  }

  &--site {
    display: flex;
    justify-content: flex-start;
    width: 39%;

    .footer__nav-link {
      user-select: none;
    }

    @include vp-1023 {
      width: 100%;
      flex-direction: column;
      padding-right: 75px;
    }

    @include vp-767 {
      padding-right: 0;
    }
  }

  &--contacts {
    width: 27%;

    @include vp-1023 {
      width: 100%;
    }
  }
}

.footer__nav-wrapper h2 {
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  // margin: 5px 0 25px;
  margin: 6px 0 23px;
  position: relative;

  @include vp-1023 {
    margin: 5px 0 22px;
  }

  @include vp-767 {
    margin: 0;
    padding: 10px 25px 10px 0;
  }
}

.footer__nav-wrapper--contacts p {
  color: $color-default-white-opacity;
}

.footer__list-wrapper {
  width: 50%;
  margin-right: 19px;

  @include vp-1023 {
    width: 100%;
    margin: 0;
  }
}

.footer__nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;

  &--more {
    // padding-top: 46px;
    padding-top: 45px;

    @include vp-1023 {
      padding-top: 0;
    }
  }
}

.footer__nav-item {
  &:not(:last-child) {
    // margin-bottom: 8px;
    margin-bottom: 5px;

    @include vp-767 {
      margin-bottom: 17px;

      &:first-child {
        margin-top: 10px;
      }
    }
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }

  &--tel {
    &:not(:last-child) {
      // margin-bottom: 8px;
      margin-bottom: 6px;
    }

    @include vp-767 {
      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }

  &--address {
    margin-bottom: 8px;

    @include vp-767 {
      margin-bottom: 17px;
    }
  }
}

.footer__nav-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $ff-ptmono;
  color: $color-default-white-opacity;

  @include vp-767 {
    font-size: 14px;
    line-height: 25px;
  }

  @include hover {
    color: $deep-sky-blue;
  }

  @include focus {
    outline: 2px solid $deep-sky-blue;
    border-radius: 5px;
  }
}

.footer__address {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $ff-ptmono;
  color: $color-transparent;
  margin: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
    margin-top: 8px;
  }
}

.is-active[data-spoiler-toggle] {

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 17px;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: $silver;
  }

  &::after {
    transform: rotate(90deg);
  }
}

.footer__nav-wrapper.is-closed .footer__nav-list--site,
.footer__nav-wrapper.is-closed .footer__nav-list--contacts {
  display: none;
}

.is-open[data-spoiler-toggle] {
  &::after {
    display: none;
  }
}
